import kotlinx.datetime.toLocalDateTime

fun calculateWorkDays(startDate: LocalDate, endDate: LocalDate): Int {
    // Calculate the total number of days between startDate and endDate
    val totalDays = (endDate - startDate).days + 1 // Include end date

    // Calculate the number of complete weeks in the range
    val completeWeeks = totalDays / 7

    // Each complete week contributes 5 working days
    val weekdaysFromCompleteWeeks = completeWeeks * 5

    // Calculate remaining days after accounting for complete weeks
    val remainingDays = totalDays % 7

    // Determine how many of the remaining days are weekdays
    val startDayOfWeek = startDate.dayOfWeek
    val weekdaysFromRemainingDays = (0 until remainingDays).count { offset ->
        val currentDayOfWeek =
            (startDayOfWeek.isoDayNumber + offset - 1) % 7 + 1 // Adjust for 1-based index
        currentDayOfWeek in 1..5 // Check if it's a weekday (Monday to Friday)
    }

    // Total working days is the sum of weekdays from complete weeks and remaining days
    return weekdaysFromCompleteWeeks + weekdaysFromRemainingDays
}

fun getCurrentDateTime() = Clock.System.now()
    .toLocalDateTime(TimeZone.currentSystemDefault())

val timeFormat = LocalTime.Format {
    amPmHour(); char(':'); minute(); char(' '); amPmMarker("AM", "PM")
}

val dateFormat = LocalDate.Format {
    monthName(MonthNames.ENGLISH_FULL); char(' '); dayOfMonth(); char(','); char(' '); year()
}

val dateAndWeekFormat = LocalDate.Format {
    monthName(MonthNames.ENGLISH_FULL);
    char(' ');
    dayOfMonth();
    char(',');
    char(' ');
    year();
    char(' ');
    char('-');
    char(' ');
    dayOfWeek(DayOfWeekNames.ENGLISH_FULL)
}

internal fun Long.toLocalDate(): LocalDate = Instant
    .fromEpochMilliseconds(this)
    .toLocalDateTime(TimeZone.currentSystemDefault())
    .date

internal fun LocalDate.getFirstDayOfCutoff(): LocalDate {
    return if (this.dayOfMonth < 16) {
        minus(this.dayOfMonth - 1, DateTimeUnit.DAY)
    } else {
        val minus = this.dayOfMonth - 16
        minus(minus, DateTimeUnit.DAY)
    }
}

internal fun getCurrentMonth(): String =
    MonthNames.ENGLISH_FULL.names[getCurrentDateTime().monthNumber - 1]

fun LocalDate.calculateAge(): Int {
    val today = getCurrentDateTime()
    var age = today.year - this.year

    // Adjust age if the birthday hasn't occurred yet this year
    if (today.monthNumber < this.monthNumber ||
        (today.monthNumber == this.monthNumber && today.dayOfMonth < this.dayOfMonth)) {
        age--
    }

    return age
}