package repository.expense

import app.cash.paging.Pager
import app.cash.paging.createPager
import app.cash.paging.createPagingConfig
import io.ktor.client.HttpClient
import io.ktor.client.call.body
import io.ktor.client.request.delete
import io.ktor.client.request.post
import io.ktor.client.request.put
import io.ktor.client.request.setBody
import model.Expense
import model.ExpenseRequest

interface ExpenseRepository {
    fun getExpenses(searchName: String? = null): Pager<Int, Expense>

    suspend fun addExpense(expenseRequest: ExpenseRequest): Long

    suspend fun deleteExpense(id: Long)

    suspend fun updateExpense(expense: Expense)
}

internal class ExpenseRepositoryImpl(
    private val httpClient: HttpClient
) : ExpenseRepository {

    companion object {
        private const val PATIENTS = "expenses"
    }

    override fun getExpenses(searchName: String?): Pager<Int, Expense> {
        val pagingConfig = createPagingConfig(
            pageSize = 1,
            initialLoadSize = 1
        ) // TODO not sure about pageSize and initialLoadSize
        return createPager(pagingConfig) {
            ExpensePagingSource(httpClient, searchName)
        }
    }

    override suspend fun addExpense(expenseRequest: ExpenseRequest): Long {
        val response = httpClient.post(PATIENTS) {
            setBody(expenseRequest)
        }
        return response.body<Long>()
    }

    override suspend fun deleteExpense(id: Long) {
        httpClient.delete("$PATIENTS/$id")
    }

    override suspend fun updateExpense(expense: Expense) {
        httpClient.put(PATIENTS) {
            setBody(expense)
        }
    }
}