package ui.patient.newpatient

import com.arkivanov.decompose.value.Value
import model.PatientRequest

interface NewPatientComponent {
    val model: Value<Model>

    data class Model(
        val isLoading: Boolean = false,
        val snackbarMessage: String? = null,
        val image: ByteArray? = null
    )

    fun handleEvent(event: NewPatientEvent)
}

sealed class NewPatientEvent {
    data object OnBack : NewPatientEvent()
    data object OnClearSnackbar : NewPatientEvent()
    data class OnUpdateCameraImage(val image: ByteArray?) : NewPatientEvent()
    data class Add(val request: PatientRequest) : NewPatientEvent()
}