package ui.patient.newpatient

import androidx.compose.runtime.Composable
import androidx.compose.runtime.derivedStateOf
import androidx.compose.runtime.getValue
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.saveable.Saver
import androidx.compose.runtime.saveable.listSaver
import androidx.compose.runtime.saveable.rememberSaveable
import androidx.compose.runtime.setValue
import kotlinx.datetime.LocalDate
import model.PatientRequest
import util.calculateAge
import util.dateFormat

@Composable
internal fun rememberNewPatientState(): NewPatientState {
    return rememberSaveable(
        saver = NewPatientState.Saver()
    ) {
        NewPatientState()
    }
}

class NewPatientState {
    var name by mutableStateOf("")
    var birthDate: LocalDate? by mutableStateOf(null)
    var contactNumber: String? by mutableStateOf(null)
    var address: String? by mutableStateOf(null)
    var civilStatus: String? by mutableStateOf(null)
    var gender: String? by mutableStateOf(null)

    val birthDateString: String by derivedStateOf {
        birthDate?.let {
            dateFormat.format(it)
        } ?: ""
    }

    val age by derivedStateOf {
        birthDate?.let {
            val age = it.calculateAge()
            if (age > 1) {
                "$age yrs old"
            } else {
                "$age yr old"
            }
        } ?: ""
    }

    var isDatePickerDialogShown by mutableStateOf(false)
    var isCivilStatusDialogShown by mutableStateOf(false)
    var isGenderDialogShown by mutableStateOf(false)

    val isAddEnabled by derivedStateOf {
        name.isNotBlank() && birthDate != null
    }

    val patientRequest by derivedStateOf {
        PatientRequest(
            name = name,
            birthdate = requireNotNull(birthDate),
            contactNumber = contactNumber,
            address = address,
            civilStatus = civilStatus,
            gender = gender
        )
    }

    companion object {
        fun Saver(): Saver<NewPatientState, Any> = listSaver(
            save = {
                listOf(
                    it.name,
                    it.contactNumber,
                    it.address,
                    it.civilStatus,
                    it.gender,
                    it.isDatePickerDialogShown,
                    it.isCivilStatusDialogShown,
                    it.isGenderDialogShown,
                )
            },
            restore = { value ->
                NewPatientState().apply {
                    name = value[0] as String
                    contactNumber = value[1] as String?
                    address = value[2] as String?
                    civilStatus = value[3] as String?
                    gender = value[4] as String?
                    isDatePickerDialogShown = value[5] as Boolean
                    isCivilStatusDialogShown = value[6] as Boolean
                    isGenderDialogShown = value[7] as Boolean
                }
            }
        )
    }
}