package ui.patient.newpatient

import com.arkivanov.decompose.ComponentContext
import com.arkivanov.decompose.value.MutableValue
import com.arkivanov.decompose.value.Value
import com.arkivanov.decompose.value.update
import component.componentCoroutineScope
import kotlinx.coroutines.launch
import model.PatientRequest
import org.koin.core.component.KoinComponent
import org.koin.core.component.inject
import repository.patient.PatientRepository
import ui.patient.newpatient.NewPatientComponent.Model

internal class DefaultNewPatientComponent(
    componentContext: ComponentContext,
    private val onBack: () -> Unit,
    private val refreshPatients: () -> Unit
) : ComponentContext by componentContext, NewPatientComponent, KoinComponent {

    private val repository by inject<PatientRepository>()

    private val scope = componentCoroutineScope()

    private val state = MutableValue(Model())
    override val model: Value<Model> = state

    override fun handleEvent(event: NewPatientEvent) {
        when (event) {
            is NewPatientEvent.Add -> add(event.request)
            is NewPatientEvent.OnBack -> onBack()
            is NewPatientEvent.OnClearSnackbar -> cleanSnackbar()
            is NewPatientEvent.OnUpdateCameraImage -> updateCameraImage(event.image)
        }
    }

    private fun updateCameraImage(image: ByteArray?) {
        state.update { it.copy(image = image) }
    }

    private fun cleanSnackbar() {
        state.update { it.copy(snackbarMessage = null) }
    }

    private fun add(request: PatientRequest) {
        scope.launch {
            state.update { it.copy(isLoading = true) }
            try {
                repository.addPatient(request, model.value.image)
                refreshPatients()
            } catch (e: dynamic) { // Replace to `dynamic` when building with web JS
                state.update { it.copy(snackbarMessage = "Something went wrong") }
            } finally {
                state.update { it.copy(isLoading = false) }
            }
        }
    }
}