package repository.procedure

import app.cash.paging.Pager
import app.cash.paging.createPager
import app.cash.paging.createPagingConfig
import io.ktor.client.HttpClient
import io.ktor.client.call.body
import io.ktor.client.request.delete
import io.ktor.client.request.post
import io.ktor.client.request.put
import io.ktor.client.request.setBody
import model.Procedure
import model.ProcedureRequest

interface ProcedureRepository {
    fun getProcedures(searchName: String? = null): Pager<Int, Procedure>

    suspend fun addProcedure(procedureRequest: ProcedureRequest): Long

    suspend fun deleteProcedure(id: Long)

    suspend fun updateProcedure(procedure: Procedure)
}

internal class ProcedureRepositoryImpl(
    private val httpClient: HttpClient
) : ProcedureRepository {

    companion object {
        private const val PATIENTS = "procedures"
    }

    override fun getProcedures(searchName: String?): Pager<Int, Procedure> {
        val pagingConfig = createPagingConfig(
            pageSize = 1,
            initialLoadSize = 1
        ) // TODO not sure about pageSize and initialLoadSize
        return createPager(pagingConfig) {
            ProcedurePagingSource(httpClient, searchName)
        }
    }

    override suspend fun addProcedure(procedureRequest: ProcedureRequest): Long {
        val response = httpClient.post(PATIENTS) {
            setBody(procedureRequest)
        }
        return response.body<Long>()
    }

    override suspend fun deleteProcedure(id: Long) {
        httpClient.delete("$PATIENTS/$id")
    }

    override suspend fun updateProcedure(procedure: Procedure) {
        httpClient.put(PATIENTS) {
            setBody(procedure)
        }
    }
}