@Composable
fun NewPatientContent(
    component: NewPatientComponent,
    modifier: Modifier = Modifier,
) {
    val model by component.model.subscribeAsState()
    val newPatientState = rememberNewPatientState()

    val snackbarHostState = remember { SnackbarHostState() }

    // Show snackbar
    model.snackbarMessage?.let {
        LaunchedEffect(it) {
            snackbarHostState.showSnackbar(it)
            component.handleEvent(NewPatientEvent.OnClearSnackbar)
        }
    }

    NewPatientContent(
        onEvent = { component.handleEvent(it) },
        state = newPatientState,
        snackbarHostState = snackbarHostState,
        model = model,
        modifier = modifier
    )
}

@Composable
fun NewPatientContent(
    onEvent: (NewPatientEvent) -> Unit,
    state: NewPatientState,
    snackbarHostState: SnackbarHostState,
    model: NewPatientComponent.Model,
    modifier: Modifier = Modifier
) {
    Scaffold(
        modifier = modifier.fillMaxSize(),
        snackbarHost = { SnackbarHost(snackbarHostState) },
        topBar = {
            TopAppBar(
                title = "New Patient",
                onBack = { onEvent(NewPatientEvent.OnBack) }
            )
        },
        bottomBar = {
            Button(
                modifier = Modifier.padding(16.dp).fillMaxWidth(),
                onClick = { onEvent(NewPatientEvent.Add(state.patientRequest)) },
                enabled = state.isAddEnabled,
                content = {
                    Text("Add")
                }
            )
        }
    ) { contentPadding ->
        NewPatientForm(
            onEvent = onEvent,
            snackbarHostState = snackbarHostState,
            state = state,
            model = model,
            modifier = Modifier.padding(contentPadding)
        )
    }
}

@OptIn(ExperimentalMaterial3Api::class)
@Composable
fun NewPatientForm(
    onEvent: (NewPatientEvent) -> Unit,
    snackbarHostState: SnackbarHostState,
    model: NewPatientComponent.Model,
    state: NewPatientState,
    modifier: Modifier = Modifier
) {
    when {
        model.isLoading -> LoadingDialog()
        state.isDatePickerDialogShown -> {
            DatePickerDialog(
                onDismiss = { state.isDatePickerDialogShown = false },
                onConfirm = {
                    state.birthDate = it
                    state.isDatePickerDialogShown = false
                },
                selectedDate = state.birthDate ?: getCurrentDateTime().date,
                isPickerMode = false,
                showModeToggle = false
            )
        }

        state.isCivilStatusDialogShown -> {
            SimpleBottomSheetDialog(
                onClick = { state.civilStatus = it },
                onDismiss = { state.isCivilStatusDialogShown = false },
                title = "Select Civil Status",
                items = civilStatuses,
            )
        }

        state.isGenderDialogShown -> {
            SimpleBottomSheetDialog(
                onClick = { state.gender = it },
                onDismiss = { state.isGenderDialogShown = false },
                title = "Select Gender",
                items = genderOptions,
            )
        }
    }

    val scope = rememberCoroutineScope()

    Column(
        modifier = modifier
            .verticalScroll(rememberScrollState())
            .padding(16.dp),
        verticalArrangement = Arrangement.spacedBy(16.dp)
    ) {
        Camera(
            modifier = Modifier.fillMaxWidth(),
            onImageCaptured = { onEvent(NewPatientEvent.OnUpdateCameraImage(it)) },
            onClearImage = { onEvent(NewPatientEvent.OnUpdateCameraImage(null)) },
            onFailedToCaptureImage = {
                scope.launch {
                    snackbarHostState.showSnackbar("Failed to capture image")
                }
            },
            currentImagePath = null
        )

        OutlinedTextField(
            modifier = Modifier.fillMaxWidth(),
            value = state.name,
            onValueChange = { state.name = it },
            singleLine = true,
            label = { Text("Name") },
            keyboardOptions = KeyboardOptions(imeAction = ImeAction.Next),
        )

        Row(
            modifier = Modifier.fillMaxWidth(),
            verticalAlignment = Alignment.CenterVertically,
            horizontalArrangement = Arrangement.spacedBy(16.dp)
        ) {
            ClickableTextField(
                modifier = Modifier.weight(1f),
                onClick = { state.isDatePickerDialogShown = true },
                value = state.birthDateString,
                label = "Birthdate",
            )

            if (state.birthDate != null) {
                Text(state.age)
            }
        }

        OutlinedTextField(
            modifier = Modifier.fillMaxWidth(),
            value = state.contactNumber.orEmpty(),
            onValueChange = { state.contactNumber = it },
            singleLine = true,
            label = { Text("Contact Number") },
            keyboardOptions = KeyboardOptions(
                imeAction = ImeAction.Next,
                keyboardType = KeyboardType.Phone
            ),
        )

        OutlinedTextField(
            modifier = Modifier.fillMaxWidth(),
            value = state.address.orEmpty(),
            onValueChange = { state.address = it },
            singleLine = true,
            label = { Text("Address") },
            keyboardOptions = KeyboardOptions(imeAction = ImeAction.Next),
        )

        ClickableTextField(
            modifier = Modifier.fillMaxWidth(),
            onClick = { state.isCivilStatusDialogShown = true },
            value = state.civilStatus.orEmpty(),
            label = "Civil Status",
        )

        ClickableTextField(
            modifier = Modifier.fillMaxWidth(),
            onClick = { state.isGenderDialogShown = true },
            value = state.gender.orEmpty(),
            label = "Gender",
        )
    }
}