        name = "Juan",
        salary = 2,
        hasImage = false,
    ),
)

internal class EmployeeRepositoryImpl(
    private val httpClient: HttpClient
) : EmployeeRepository {

    companion object {
        private const val EMPLOYEES = "employees"
    }

    override suspend fun getEmployees(): List<Employee> {
        /*val response = httpClient.get(URL_ATTENDANCE)
        val attendanceLog = response.body<List<AttendanceLog>>()
        return attendanceLog*/

        val a = Employee(
            id = 0L,
            nfc = "3116900580",
            name = "Juan",
            salary = 2,
            hasImage = false,
        )
        val s: MutableList<Employee> = mutableListOf()
        repeat(100) {
            s.add(
                a.copy(
                    id = it.toLong(),
                    name = "juan $it"
                )
            )
        }

        return s
    }

    override suspend fun addEmployee(
        employeeRequest: EmployeeRequest,
        image: ByteArray?
    ): Long {
        val request = Json.encodeToString(employeeRequest)
        val response: HttpResponse = httpClient.post(EMPLOYEES) {
            setBody(
                MultiPartFormDataContent(
                    formData {
                        append("data", request)
                        if (image != null) {
                            append("image", image, Headers.build {
                                append(HttpHeaders.ContentType, "image/png")
                                append(HttpHeaders.ContentDisposition, "filename=\"employee.png\"")
                            })
                        }
                    }
                )
            )
        }
        return response.body()
    }

    override suspend fun deleteEmployee(id: Long) {
        httpClient.delete("$EMPLOYEES/$id")
    }

    override suspend fun updateEmployee(employee: Employee, image: ByteArray?) {
        val request = Json.encodeToString(employee)
        httpClient.put(EMPLOYEES) {
            setBody(
                MultiPartFormDataContent(
                    formData {
                        append("data", request)
                        if (image != null) {
                            append("image", image, Headers.build {
                                append(HttpHeaders.ContentType, "image/png")
                                append(HttpHeaders.ContentDisposition, "filename=\"employee.png\"")
                            })
                        }
                    }
                )
            )
        }
    }
}