package di

import com.arkivanov.decompose.ComponentContext
import org.koin.dsl.module
import platformModule
import repository.AccountRepository
import repository.AccountRepositoryImpl
import repository.BonusRepository
import repository.BonusRepositoryImpl
import repository.doctor.DoctorRepository
import repository.doctor.DoctorRepositoryImpl
import repository.EmployeeRepository
import repository.EmployeeRepositoryImpl
import repository.attendance.AttendanceRepository
import repository.attendance.AttendanceRepositoryImpl
import repository.expense.ExpenseRepository
import repository.expense.ExpenseRepositoryImpl
import repository.patient.PatientRepository
import repository.patient.PatientRepositoryImpl
import repository.procedure.ProcedureRepository
import repository.procedure.ProcedureRepositoryImpl
import ui.patient.newpatient.DefaultNewPatientComponent
import ui.patient.newpatient.NewPatientComponent
import ui.root.DefaultRootComponent
import ui.root.RootComponent

fun appModule() = listOf(commonModule, platformModule(), networkModule)

val commonModule = module {
    single<AttendanceRepository> {
        AttendanceRepositoryImpl(
            httpClient = get()
        )
    }

    single<PatientRepository> {
        PatientRepositoryImpl(
            httpClient = get()
        )
    }

    single<ProcedureRepository> {
        ProcedureRepositoryImpl(
            httpClient = get()
        )
    }

    single<ExpenseRepository> {
        ExpenseRepositoryImpl(
            httpClient = get()
        )
    }

    single<BonusRepository> {
        BonusRepositoryImpl(
            httpClient = get()
        )
    }

    single<DoctorRepository> {
        DoctorRepositoryImpl(
            httpClient = get()
        )
    }

    single<AccountRepository> {
        AccountRepositoryImpl(
            httpClient = get()
        )
    }

    single<EmployeeRepository> {
        EmployeeRepositoryImpl(
            httpClient = get()
        )
    }
}

fun createRootComponent(
    componentContext: ComponentContext
): RootComponent {
    return DefaultRootComponent(componentContext)
}

fun createPatientComponent(
    onSuccess: () -> Unit,
    componentContext: ComponentContext
): NewPatientComponent {
    return DefaultNewPatientComponent(
        componentContext = componentContext,
        onBack = {},
        refreshPatients = onSuccess
    )
}