package common

import androidx.compose.foundation.layout.padding
import androidx.compose.material3.DatePicker
import androidx.compose.material3.DateRangePicker
import androidx.compose.material3.DisplayMode
import androidx.compose.material3.ExperimentalMaterial3Api
import androidx.compose.material3.Text
import androidx.compose.material3.rememberDatePickerState
import androidx.compose.material3.rememberDateRangePickerState
import androidx.compose.runtime.Composable
import androidx.compose.runtime.derivedStateOf
import androidx.compose.runtime.getValue
import androidx.compose.runtime.remember
import androidx.compose.ui.Modifier
import androidx.compose.ui.unit.dp
import kotlinx.datetime.Clock
import kotlinx.datetime.Instant
import kotlinx.datetime.LocalDate
import kotlinx.datetime.TimeZone
import kotlinx.datetime.toLocalDateTime
import util.getCurrentDateTime
import util.toLocalDate

@OptIn(ExperimentalMaterial3Api::class)
@Composable
fun DatePickerDialog(
    onConfirm: (LocalDate) -> Unit,
    onDismiss: () -> Unit,
    modifier: Modifier = Modifier,
    selectedDate: LocalDate = Clock.System.now()
        .toLocalDateTime(TimeZone.currentSystemDefault()).date,
    showModeToggle: Boolean = true,
    isPickerMode: Boolean = true
) {
    val selectedDateMillis: Long = selectedDate.toEpochDays().toLong() * 24 * 60 * 60 * 1000

    val state = rememberDatePickerState(
        initialDisplayMode = if (isPickerMode) {
            DisplayMode.Picker
        } else {
            DisplayMode.Input
        },
        initialSelectedDateMillis = selectedDateMillis,
    )

    val currentDate: LocalDate? = remember(state.selectedDateMillis) {
        state.selectedDateMillis?.let { selected ->
            Instant
                .fromEpochMilliseconds(selected)
                .toLocalDateTime(TimeZone.currentSystemDefault())
                .date
        }
    }

    val isConfirmEnabled by derivedStateOf {
        currentDate != null
    }

    Dialog(
        modifier = modifier.padding(16.dp),
        onDismiss = onDismiss,
        isConfirmEnabled = isConfirmEnabled,
        onConfirm = {
            if (currentDate != null) {
                onConfirm(currentDate)
            }
        },
    ) {
        DatePicker(
            state = state,
            showModeToggle = true,
            title = { Text("Select date") }
        )
    }
}

@OptIn(ExperimentalMaterial3Api::class)
@Composable
fun DateRangePickerDialog(
    onConfirm: (LocalDate, LocalDate) -> Unit,
    onDismiss: () -> Unit,
    modifier: Modifier = Modifier,
    selectedStartDate: LocalDate = getCurrentDateTime().date,
    selectedEndDate: LocalDate = getCurrentDateTime().date
) {
    val selectedStartDateMillis: Long =
        selectedStartDate.toEpochDays().toLong() * 24 * 60 * 60 * 1000
    val selectedEndDateMillis: Long = selectedEndDate.toEpochDays().toLong() * 24 * 60 * 60 * 1000

    val state = rememberDateRangePickerState(
        initialSelectedStartDateMillis = selectedStartDateMillis,
        initialSelectedEndDateMillis = selectedEndDateMillis
    )

    Dialog(
        modifier = modifier,
        onDismiss = onDismiss,
        isConfirmEnabled = state.selectedStartDateMillis != null && state.selectedEndDateMillis != null,
        onConfirm = {
            val startDate = state.selectedStartDateMillis!!.toLocalDate()
            val endDate = state.selectedEndDateMillis!!.toLocalDate()
            onConfirm(startDate, endDate)
        },
    ) {
        DateRangePicker(
            state = state,
            showModeToggle = true,
            title = { Text("Select date") }
        )
    }
}
