package model

import kotlinx.datetime.LocalDate
import kotlinx.serialization.Serializable
import util.calculateAge
import util.dateFormat

@Serializable
data class Patient(
    val id: Long,
    val name: String,
    val birthdate: LocalDate,
    val contactNumber: String?,
    val address: String?,
    val civilStatus: String?,
    val gender: String?,
    val hasImage: Boolean
) {
    fun imagePath(baseUrl: String): String? = if (hasImage) {
        "$baseUrl/images/patients/$id.png"
    } else {
        null
    }

    val dateString = dateFormat.format(birthdate)
}

@Serializable
data class PatientRequest(
    val name: String,
    val birthdate: LocalDate,
    val contactNumber: String?,
    val address: String?,
    val civilStatus: String?,
    val gender: String?
)

val civilStatuses = listOf("Single", "Married", "Divorced", "Widowed")
val genderOptions = listOf("Male", "Female")