package repository

import io.ktor.client.HttpClient
import io.ktor.client.call.body
import io.ktor.client.request.get
import io.ktor.client.request.post
import io.ktor.client.request.setBody
import model.AccountResponse
import model.LoginRequest

internal interface AccountRepository {

    suspend fun getAccounts(): List<AccountResponse>

    suspend fun getAccount(id: Long): AccountResponse

    suspend fun login(loginRequest: LoginRequest): Long
}

internal class AccountRepositoryImpl(
    private val httpClient: HttpClient
) : AccountRepository {

    companion object {
        private const val URL_ACCOUNTS = "accounts"
    }

    override suspend fun getAccounts(): List<AccountResponse> {
//        val accounts = response.body<List<Product>>()
        return listOf(
            AccountResponse(2, "Juan", "juan"),
            AccountResponse(3, "Pedro", "pedro"),
        )
    }

    override suspend fun getAccount(id: Long): AccountResponse {
        return httpClient.get("$URL_ACCOUNTS/$id").body()
    }

    override suspend fun login(loginRequest: LoginRequest): Long {
        val response = httpClient.post("$URL_ACCOUNTS/login") {
            setBody(loginRequest)
        }
        val accountResponse = response.body<AccountResponse>()
        return accountResponse.id
    }
}