package repository.attendance

import app.cash.paging.Pager
import app.cash.paging.createPager
import app.cash.paging.createPagingConfig
import io.ktor.client.HttpClient
import io.ktor.client.call.body
import io.ktor.client.request.get
import io.ktor.client.request.parameter
import io.ktor.client.request.post
import io.ktor.client.request.setBody
import io.ktor.http.parameters
import kotlinx.coroutines.delay
import kotlinx.datetime.DateTimeUnit
import kotlinx.datetime.LocalDate
import kotlinx.datetime.LocalTime
import kotlinx.datetime.plus
import kotlinx.datetime.until
import model.AttendanceLog
import model.AttendanceLogRequest
import model.EmployeeAttendance
import model.EmployeeLog
import util.getCurrentDateTime

interface AttendanceRepository {

    fun getAttendanceLogs(searchName: String? = null): Pager<Int, AttendanceLog>

    suspend fun addAttendance(attendanceLogRequest: AttendanceLogRequest): AttendanceLog

    suspend fun getAttendanceEmployees(
        selectedDate: LocalDate
    ): List<EmployeeAttendance>

    suspend fun getEmployeeLogs(
        employeeId: Long,
        startDate: LocalDate,
        endDate: LocalDate
    ): List<EmployeeLog>
}

internal class AttendanceRepositoryImpl(
    private val httpClient: HttpClient
) : AttendanceRepository {

    companion object {
        private const val ATTENDANCES = "attendances"
    }

    override fun getAttendanceLogs(searchName: String?): Pager<Int, AttendanceLog> {
        val pagingConfig = createPagingConfig(
            pageSize = 1,
            initialLoadSize = 1
        ) // TODO not sure about pageSize and initialLoadSize
        return createPager(pagingConfig) {
            AttendancePagingSource(httpClient, searchName)
        }
    }

    override suspend fun getAttendanceEmployees(
        selectedDate: LocalDate
    ): List<EmployeeAttendance> {
        val response = httpClient.get("$ATTENDANCES/employees") {
            parameters {
                parameter("date", selectedDate.toString())
            }
        }

        return response.body()
    }

    override suspend fun addAttendance(attendanceLogRequest: AttendanceLogRequest): AttendanceLog {
        val response = httpClient.post(ATTENDANCES) {
            setBody(attendanceLogRequest)
        }
        return response.body()
    }

    override suspend fun getEmployeeLogs(
        employeeId: Long,
        startDate: LocalDate,
        endDate: LocalDate
    ): List<EmployeeLog> {
        delay(1000)
        val employeeLog = EmployeeLog(
            id = 1,
            date = getCurrentDateTime().date,
            clockIn = null,
            clockOut = null,
            hoursWorked = null,
            minutesWorked = null
        )
        val employees: MutableList<EmployeeLog> = mutableListOf()

        val dayDifference = startDate.until(endDate, DateTimeUnit.DAY) + 1

        repeat(dayDifference) {
            val currentDate = startDate.plus(it, DateTimeUnit.DAY)

            employees.add(
                employeeLog.copy(
                    id = it.toLong(),
                    date = currentDate,
                    clockIn = LocalTime(8, 0),
                    clockOut = LocalTime(5, 0),
                    hoursWorked = 8,
                    minutesWorked = 0
                )
            )
        }
        return employees
    }
}