import model.AppConfig
import org.koin.core.module.Module
import org.koin.dsl.module
import repository.AppRepository
import repository.attendance.FakeLocalAttendanceRepository
import repository.attendance.LocalAttendanceRepository
import ui.root.KeyListener
import util.FakeKeyListener

class WasmPlatform : Platform {
    override val name: String = "Web with Kotlin/Wasm"
}

actual fun platformModule(): Module =
    module {
        single<Platform> { WasmPlatform() }

        single<AppRepository> {
            FakeWebAppRepository()
        }

        single<KeyListener> {
            FakeKeyListener()
        }

        single<LocalAttendanceRepository> {
            FakeLocalAttendanceRepository()
        }
    }

class FakeWebAppRepository : AppRepository {
    override fun getAppConfig(): AppConfig {
        return AppConfig(
            url = "enabled-properly-kangaroo.ngrok-free.app",
            port = 0,
            isSecured = true,
            isAttendanceMode = true,
            isDarkMode = true,
        )
    }

    override fun updateAppConfig(appConfig: AppConfig) {
//        TODO("Not yet implemented")
    }
}