package repository.doctor

import app.cash.paging.Pager
import app.cash.paging.createPager
import app.cash.paging.createPagingConfig
import io.ktor.client.HttpClient
import io.ktor.client.call.body
import io.ktor.client.request.delete
import io.ktor.client.request.get
import io.ktor.client.request.post
import io.ktor.client.request.put
import io.ktor.client.request.setBody
import model.Doctor
import model.DoctorRequest

interface DoctorRepository {
    suspend fun getAllDoctors(): List<Doctor>

    fun getDoctors(searchName: String? = null): Pager<Int, Doctor>

    suspend fun addDoctor(doctorRequest: DoctorRequest): Long

    suspend fun deleteDoctor(id: Long)

    suspend fun updateDoctor(doctor: Doctor)
}

internal class DoctorRepositoryImpl(
    private val httpClient: HttpClient
) : DoctorRepository {

    companion object {
        private const val DOCTORS = "doctors"
    }

    override suspend fun getAllDoctors(): List<Doctor> {
        val response = httpClient.get(DOCTORS)
        return response.body()
    }

    override fun getDoctors(searchName: String?): Pager<Int, Doctor> {
        val pagingConfig = createPagingConfig(
            pageSize = 1,
            initialLoadSize = 1
        ) // TODO not sure about pageSize and initialLoadSize
        return createPager(pagingConfig) {
            DoctorPagingSource(httpClient, searchName)
        }
    }

    override suspend fun addDoctor(doctorRequest: DoctorRequest): Long {
        val response = httpClient.post(DOCTORS) {
            setBody(doctorRequest)
        }
        return response.body<Long>()
    }

    override suspend fun deleteDoctor(id: Long) {
        httpClient.delete("$DOCTORS/$id")
    }

    override suspend fun updateDoctor(doctor: Doctor) {
        httpClient.put(DOCTORS) {
            setBody(doctor)
        }
    }
}