package repository.patient

import app.cash.paging.Pager
import app.cash.paging.createPager
import app.cash.paging.createPagingConfig
import io.ktor.client.HttpClient
import io.ktor.client.call.body
import io.ktor.client.request.delete
import io.ktor.client.request.forms.MultiPartFormDataContent
import io.ktor.client.request.forms.formData
import io.ktor.client.request.post
import io.ktor.client.request.put
import io.ktor.client.request.setBody
import io.ktor.client.statement.HttpResponse
import io.ktor.http.Headers
import io.ktor.http.HttpHeaders
import kotlinx.serialization.encodeToString
import kotlinx.serialization.json.Json
import model.Patient
import model.PatientRequest

interface PatientRepository {
    fun getPatients(searchName: String? = null): Pager<Int, Patient>

    suspend fun addPatient(
        patientRequest: PatientRequest,
        image: ByteArray?
    ): Long

    suspend fun deletePatient(id: Long)

    suspend fun updatePatient(patient: Patient, image: ByteArray?)
}

internal class PatientRepositoryImpl(
    private val httpClient: HttpClient
) : PatientRepository {

    companion object {
        private const val PATIENTS = "patients"
    }

    override fun getPatients(searchName: String?): Pager<Int, Patient> {
        val pagingConfig = createPagingConfig(
            pageSize = 1,
            initialLoadSize = 1
        ) // TODO not sure about pageSize and initialLoadSize
        return createPager(pagingConfig) {
            PatientPagingSource(httpClient, searchName)
        }
    }

    override suspend fun addPatient(
        patientRequest: PatientRequest,
        image: ByteArray?
    ): Long {
        val request = Json.encodeToString(patientRequest)
        val response: HttpResponse = httpClient.post(PATIENTS) {
            setBody(
                MultiPartFormDataContent(
                    formData {
                        append("data", request)
                        if (image != null) {
                            append("image", image, Headers.build {
                                append(HttpHeaders.ContentType, "image/png")
                                append(HttpHeaders.ContentDisposition, "filename=\"patient.png\"")
                            })
                        }
                    }
                )
            )
        }
        return response.body()
    }

    override suspend fun deletePatient(id: Long) {
        httpClient.delete("$PATIENTS/$id")
    }

    override suspend fun updatePatient(patient: Patient, image: ByteArray?) {
        val request = Json.encodeToString(patient)
        httpClient.put(PATIENTS) {
            setBody(
                MultiPartFormDataContent(
                    formData {
                        append("data", request)
                        if (image != null) {
                            append("image", image, Headers.build {
                                append(HttpHeaders.ContentType, "image/png")
                                append(HttpHeaders.ContentDisposition, "filename=\"patient.png\"")
                            })
                        }
                    }
                )
            )
        }
    }
}