package repository

import io.ktor.client.HttpClient
import io.ktor.client.call.body
import io.ktor.client.request.get
import io.ktor.client.request.parameter
import io.ktor.http.parameters
import model.Bonus

internal interface BonusRepository {

    suspend fun getBonus(
        year: Int,
        month: Int
    ): Bonus
}

internal class BonusRepositoryImpl(
    private val httpClient: HttpClient
) : BonusRepository {

    companion object {
        private const val URL_BONUS = "bonus"
    }

    override suspend fun getBonus(year: Int, month: Int): Bonus {
        val response = httpClient.get(URL_BONUS) {
            parameters {
                parameter("year", year)
                parameter("month", month)
            }
        }
        return response.body()
    }
}